* {
  margin: 0;
}

html {
  --black-color: #000;
  --blue-color: #00acdc;
  font-family: Roboto, sans-serif;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo__img {
  width: 40px;
  height: 40px;
}

.nav__links {
  padding: 0;
  display: flex;
}

.nav__links li {
  margin: 0 10px;
  font-weight: 500;
  list-style-type: none;
  position: relative;
}

.nav__links :first-child {
  margin-left: 0;
}

.nav__links :last-child {
  margin-right: 0;
}

.nav__link {
  color: var(--black-color);
  text-transform: uppercase;
  font-size: 12px;
  line-height: 60px;
  text-decoration: none;
  display: inline-block;
}

.nav__link:hover, .is-active {
  color: var(--blue-color);
}

.is-active:after {
  content: "";
  background-color: var(--blue-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

